function getFieldMap(tableName: string): { [key: string]: string } {
  switch (tableName) {
    case "Contacts":
      return {
        Name: "name",
        Role: "role",
        Organisation: "organisation",
        Email: "email",
        Tags: "tags",
        Notes: "notes",
      };
    case "Log Entries":
      return {
        Date: "date",
        Summary: "summary",
        Content: "content",
        Tags: "tags",
        "Log Type": "logType",
        Contacts: "contacts",
      };
    case "Snapshots":
      return {
        Title: "title",
        Date: "date",
        Content: "content",
        "Key Updates": "keyUpdates",
        "Phase ID": "phaseId",
      };
    case "Threads":
      return {
        Title: "title",
        Summary: "summary",
        Content: "content",
        Tags: "tags",
        Date: "date",
        Contacts: "contacts",
        Experiments: "experiments",
        Outputs: "outputs",
      };
    default:
      return {};
  }
}

function filterMappedFields(fields: Record<string, any>, tableName: string): Record<string, any> {
  const fieldMap = getFieldMap(tableName);
  const mapped: Record<string, any> = {};
  for (const key in fields) {
    if (fieldMap[key]) {
      mapped[fieldMap[key]] = fields[key];
    }
  }
  return mapped;
}

export { getFieldMap, filterMappedFields };
